@font-face {
  font-family: Poppins;
  font-display: swap;
  font-weight: 300;
  src: url("fonts/Poppins-Thin.woff2") format('woff2');
}

@font-face {
  font-family: Poppins;
  font-display: swap;
  font-weight: 400;
  src: url("fonts/Poppins-Regular.woff2") format('woff2');
}

@font-face {
  font-family: Poppins;
  font-display: swap;
  font-weight: 500;
  src: url("fonts/Poppins-Medium.woff2") format('woff2');
}

@font-face {
  font-family: Poppins;
  font-display: swap;
  font-weight: 700;
  src: url("fonts/Poppins-Bold.woff2") format('woff2');
}

@font-face {
  font-family: Poppins;
  font-display: swap;
  font-weight: 900;
  src: url("fonts/Poppins-Black.woff2") format('woff2');
}

body {
  font-family: Poppins, sans-serif;
}

.container {
  width: 1830px;
  margin-left: auto;
  margin-right: auto;
}

.logo {
  font-size: 30px;
  font-weight: 500;
  line-height: 1.5;
  color: #000000;
}

.heading {
  font-size: 30px;
  font-weight: 700;
  line-height: 1.5;
  text-transform: uppercase;
}

.heading--blue {
  color: rgba(34, 52, 61, 1)
}

.heading--gray{
  color: rgba(34, 52, 61, 0.5)
}